import { useEffect, useState } from 'react';

export const usePromise = <T>(promise: Promise<T>) => {
  const [value, setValue] = useState<T>(null as any);

  useEffect(() => {
    const promiseFromContext = promise;
    promiseFromContext.then((result) => {
      if (promiseFromContext === promise) setValue(result);
    });
  }, [promise]);

  return value;
};
